.modal__background {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s ease;
}
.modal__background.open {
  opacity: 1;
  pointer-events: auto;
}
.modal__background .modal {
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translate(-50%, -50%);
  width: 1280px;
  height: 782px;
  max-width: calc(100vw - 40px);
  max-height: calc(100vh - 20px);
  background-image: linear-gradient(to right top, rgb(81, 16, 43), rgb(39, 39, 42));
  border-radius: 22px;
  transition: all 0.5s ease 0s;
}
.modal__background .modal.open {
  top: 50%;
}
.modal__background .modal .modal__content {
  height: 100%;
}
.modal__background .modal .modal__content .modal__header {
  display: flex;
  align-items: center;
  height: 62px;
  padding: 0 16px 0 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}
.modal__background .modal .modal__content .modal__header .modal__title {
  flex-grow: 1;
  font-size: 24px;
  font-weight: 700;
}
.modal__background .modal .modal__content .modal__header .modal__cross {
  display: block;
  width: 30px;
  height: 30px;
  opacity: 0.6;
  cursor: pointer;
  transition: all 0.2s ease 0s;
}
.modal__background .modal .modal__content .modal__header .modal__cross:hover {
  opacity: 1;
  transition: all 0.2s ease 0s;
}
.modal__background .modal .modal__content .modal__video {
  width: 100%;
  height: calc(100% - 62px);
  max-height: calc((100vw - 40px) / 16 * 9);
}
.modal__background .modal .modal__content .modal__video iframe {
  width: 100%;
  height: 100%;
  border: none;
}

@media (max-width: 1319.98px) {
  .modal__background .modal {
    height: calc((100vw - 40px) / 16 * 9 + 62px);
  }
}
@media (max-width: 599.98px) {
  .modal__background .modal {
    height: 100%;
    max-width: 100vw;
    max-height: 100vh;
    border-radius: 0;
  }
}