.buyTicketPage {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto 120px;
}
.buyTicketPage .buyTicketPage__header {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  padding: 20px 0px;
  display: flex;
  align-items: center;
  gap: 30px;
  margin: 0 0 20px 0;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage__image {
  max-width: 60px;
  border-radius: 5px;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 700;
  margin: 0 0 6px 0;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 5px 40px;
  font-size: 16px;
  line-height: 24px;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__info .flex__icon-text {
  display: flex;
  align-items: center;
  gap: 0 5px;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__info .flex__icon-text img {
  width: 30px;
  height: 30px;
  opacity: 0.6;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__info .flex__icon-text .icon_video {
  padding: 3px;
}
.buyTicketPage .buyTicketPage__header .buyTicketPage-header__info .buyTicketPage-header__age {
  height: 33px;
  width: 33px;
  font-size: 14px;
  padding: 2px 3px;
  border: 2px solid rgb(119, 118, 119);
  border-radius: 50%;
}
.buyTicketPage .buyTicketPage__table {
  position: relative;
  display: flex;
  gap: 40px;
}
.buyTicketPage .buyTicketPage__table.loading {
  opacity: 0.5;
}
.buyTicketPage .buyTicketPage__table .loader {
  position: absolute;
  inset: 0px;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall {
  width: 60%;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .loader {
  position: absolute;
  inset: 0px;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__screen {
  width: 90%;
  margin: 0 auto 15%;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__seats {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin: 0 0 100px 0;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__example {
  display: flex;
  gap: 40px;
  margin: 0 0 10px 0;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__example .buyTicketPage-example__img-span {
  display: flex;
  align-items: flex-end;
  gap: 15px;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__example .buyTicketPage-example__img-span img {
  width: 30px;
  height: 30px;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__example .buyTicketPage-example__img-span img.empty {
  opacity: 0.4;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__hall .buyTicketPage__example .buyTicketPage-example__img-span span {
  color: rgba(255, 255, 255, 0.35);
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket {
  width: 40%;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__title {
  font-size: 24px;
  font-weight: 24;
  font-weight: 700;
  margin: 5px 0 40px 0;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__location {
  font-size: 17px;
  font-weight: 500;
  margin: 0 0 15px 0;
  opacity: 0.5;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__types {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  margin: 0 0 25px 0;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__text {
  font-size: 14px;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.35);
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__text a {
  color: rgb(28, 156, 223);
  transition: all 0.2s ease 0s;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__text a:hover {
  color: rgb(212, 7, 84);
  transition: all 0.3s ease 0s;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__button button {
  width: 100%;
}
.buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__button button.no-active {
  cursor: default;
}
.buyTicketPage .buyTicketPage__info {
  width: 60%;
  margin: 20px 0 0 0;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__text {
  font-size: 14px;
  line-height: 25px;
  margin: 0 0 30px 0;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list {
  color: rgba(255, 255, 255, 0.35);
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list .buyTicketPage__item {
  display: flex;
  margin: 0 0 8px 0;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list .buyTicketPage__item span {
  font-size: 12px;
  line-height: 25px;
  margin: 0 15px 0 0;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list .buyTicketPage__item p {
  font-size: 14px;
  line-height: 25px;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list .buyTicketPage__item a {
  color: rgb(28, 156, 223);
  transition: all 0.2s ease 0s;
}
.buyTicketPage .buyTicketPage__info .buyTicketPage__list .buyTicketPage__item a:hover {
  color: rgb(212, 7, 84);
  transition: all 0.3s ease 0s;
}

@media (max-width: 1023.98px) {
  .buyTicketPage .buyTicketPage__table {
    flex-direction: column;
  }
  .buyTicketPage .buyTicketPage__table .buyTicketPage__hall {
    width: 100%;
  }
  .buyTicketPage .buyTicketPage__table .buyTicketPage__basket {
    width: 100%;
  }
  .buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__button {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 20px 30px;
    background: rgba(39, 39, 42, 0.8);
    z-index: 3;
  }
  .buyTicketPage .buyTicketPage__table .buyTicketPage__basket .buyTicketPage-basket__button button {
    margin: 0;
  }
  .buyTicketPage .buyTicketPage__info {
    width: 100%;
  }
}
@media (max-width: 767.98px) {
  .buyTicketPage .buyTicketPage__header .buyTicketPage-header__title {
    font-size: 26px;
    line-height: 34px;
  }
}
@media (max-width: 479.98px) {
  .buyTicketPage .buyTicketPage__header .buyTicketPage__image {
    display: none;
  }
}
@media (max-width: 399.98px) {
  .buyTicketPage {
    padding: 0 15px;
  }
}