.giftCard {
  background-color: rgb(65, 65, 66);
  border-radius: 24px;
  display: flex;
  width: 100%;
}
.giftCard .giftCard__image {
  max-width: 280px;
  width: 100%;
  border-radius: 24px;
}
.giftCard .giftCard__info {
  position: relative;
  margin: 20px;
  width: 100%;
}
.giftCard .giftCard__info .giftCard__title {
  font-size: 26px;
  line-height: 28px;
  font-weight: 500;
  margin: 0 0 30px 0;
}
.giftCard .giftCard__info .giftCard__choise {
  display: flex;
  align-items: center;
  gap: 11px;
}
.giftCard .giftCard__info .giftCard__choise .giftCard__minus,
.giftCard .giftCard__info .giftCard__choise .giftCard__plus {
  width: 27px;
  height: 27px;
  opacity: 0.4;
  transition: all 0.2s ease 0s;
  cursor: pointer;
}
.giftCard .giftCard__info .giftCard__choise .giftCard__minus:hover,
.giftCard .giftCard__info .giftCard__choise .giftCard__plus:hover {
  opacity: 0.8;
  transition: all 0.3s ease 0s;
}
.giftCard .giftCard__info .giftCard__choise .giftCard__amount {
  font-size: 30px;
  font-weight: 600;
}
.giftCard .giftCard__info .giftCard__cost {
  position: absolute;
  bottom: 0px;
  right: 0px;
  font-size: 30px;
  line-height: 32px;
}

@media (max-width: 1023.98px) {
  .giftCard .giftCard__image {
    max-width: 300px;
  }
  .giftCard .giftCard__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .giftCard .giftCard__info .giftCard__title {
    font-size: 28px;
    line-height: 34px;
    margin: 0;
  }
  .giftCard .giftCard__info .giftCard__choise {
    gap: 13px;
  }
  .giftCard .giftCard__info .giftCard__choise .giftCard__minus,
  .giftCard .giftCard__info .giftCard__choise .giftCard__plus {
    width: 28px;
    height: 28px;
  }
  .giftCard .giftCard__info .giftCard__choise .giftCard__amount {
    font-size: 32px;
  }
  .giftCard .giftCard__info .giftCard__cost {
    font-size: 38px;
    line-height: 38px;
  }
}
@media (max-width: 767.98px) {
  .giftCard .giftCard__image {
    max-width: 270px;
  }
  .giftCard .giftCard__info .giftCard__title {
    font-size: 27px;
    line-height: 32px;
  }
  .giftCard .giftCard__info .giftCard__cost {
    font-size: 32px;
    line-height: 32px;
  }
}
@media (max-width: 599.98px) {
  .giftCard {
    flex-direction: column;
  }
  .giftCard .giftCard__image {
    min-width: 100%;
  }
  .giftCard .giftCard__info {
    height: 110px;
    width: auto;
  }
  .giftCard .giftCard__info .giftCard__title {
    font-size: 24px;
    line-height: 28px;
  }
}
@media (max-width: 399.98px) {
  .giftCard .giftCard__info .giftCard__title {
    font-size: 22px;
    line-height: 26px;
  }
  .giftCard .giftCard__info .giftCard__choise {
    gap: 10px;
  }
  .giftCard .giftCard__info .giftCard__choise .giftCard__minus,
  .giftCard .giftCard__info .giftCard__choise .giftCard__plus {
    width: 24px;
    height: 24px;
  }
  .giftCard .giftCard__info .giftCard__choise .giftCard__amount {
    font-size: 26px;
  }
  .giftCard .giftCard__info .giftCard__cost {
    font-size: 22px;
    line-height: 26px;
  }
}